.loading{
    width: 100vw;
    height: 100vh;
    background-color: white;
    display: grid;
    place-items: center;
    max-width: 100%;
}
.loading>div{
    width : 10vmax;
    height: 10vmax;
    border-bottom: 3px solid black;
    border-radius: 50%;
    animation: loadRotation 800ms linear infinite;
}

@keyframes loadRotation {
    to{
        transform: rotateZ(-360deg)
    }
}